import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { io } from 'socket.io-client'

import './translate-container.css'

export default function Nakdan() {
	const [disableBtn, setDisableBtn] = useState(false)
	const [q, setQ] = useState('')
	const [answers, setAnswers] = useState()
	const [socket, setSocket] = useState()

	const CancelToken = axios.CancelToken
	const source = CancelToken.source()
	const ENDPOINT =  process.env.NODE_ENV === 'development' ? 'http://localhost:4000' : 'https://hejs.cf'
	useEffect(() => {
		const s = io(ENDPOINT)
		setSocket(s)
		return () => s.disconnect()
	}, [ENDPOINT])

	useEffect(() => {
		if (!socket) return
		socket.on('nakdan', (data) => console.log(data))
	}, [socket])


	async function handleTranslate() {
		try {
			setDisableBtn(true)
			const { data } = await axios.get(`http://localhost:4000/api/nakdan?q=${q}`, {
				cancelToken: source.token,
			})
      console.log(data);
			if (data) {
				const speak = new SpeechSynthesisUtterance(data.translate)
				speechSynthesis.speak(speak)
			}
			setAnswers(data)
			setDisableBtn(false)
		} catch (error) {
			if (axios.isCancel(error)) {
				console.log('Request canceled', error.message)
			} else {
				console.log(error.name, error.message)
			}
			setDisableBtn(false)
		}
	}

	return (
		<div className='translate-container bg-dark container-fluid text-center'>
			<h2 className='my-5'>נקדן אוטומטי</h2>
			<code className='bg-light text-center text-dark rounded px-2 py-1'>
				https://hejs.cf/api/nakdan?q={q}
			</code>
      <br />
			<textarea
				cols='30'
				rows='3'
				name='q'
				id='q'
				autoFocus
				value={q}
				onChange={e => setQ(e.target.value)}
				placeholder='משפט לתרגום'
			/>
			<br />
			<button disabled={disableBtn} onClick={handleTranslate}>
				נקד
			</button>
			{answers && typeof answers === 'string' && answers  }
		</div>
	)
}
